export default class Filters {
	locations: String[];
	keywords: String[];
	maxKeywords: number;
	minKeywords: number;
	includeStatistics: boolean;
	employment: String;
	role: String;
	remote: boolean;

	constructor(
		locations: String[] = [],
		keywords: String[] = [],
		maxKeywords: number = null,
		minKeywords: number = null,
		includeStatistics: boolean = true,
		employment: String = null,
		role: String = null,
		remote: boolean = null
	) {
		this.locations = locations;
		this.keywords = keywords;
		this.maxKeywords = maxKeywords;
		this.minKeywords = minKeywords;
		this.includeStatistics = includeStatistics;
		this.employment = employment;
		this.role = role;
		this.remote = remote;
	}
}